import http from './public'
// 登陆
export const login = (params) => {
  return http.fetchPost('/api/user/login.do', params)
}
// 注册
export const register = (params) => {
  return http.fetchPost('/api/user/register.do', params)
}
// 注销
export const logout = (params) => {
  return http.fetchPost('/api/logout', params)
}
// 获取信息
export const userGet = (params) => {
  return http.fetchGet('/api/user/get.htm', params)
}
// 省份
export const provGet = (params) => {
  return http.fetchGet('/api/region/query-all-provs.htm', params)
}
// 市
export const cityGet = (params) => {
  return http.fetchGet('/api/region/query-citys-by-prov.htm', params)
}
// 县、区、
export const countyGet = (params) => {
  return http.fetchGet('/api/region/query-areas-by-city.htm', params)
}
// 更新用户信息
export const update = (params) => {
  return http.fetchPost('/api/user/update.do', params)
}
// 改密码
export const changePass = (params) => {
  return http.fetchPost('/api/user/update-password.do', params)
}
//导航栏
export const queryBar = (params) => {
  return http.fetchGet('/api/menu/query.htm', params)
}
//技术支持信息
export const querySerNav = (params) => {
  return http.fetchGet('/api/service-navigation/query.htm', params)
}
//平台标准
export const queryStandard = (params) => {
  return http.fetchGet('/api/technical-standards/query.htm', params)
}
//公司信息
export const queryCompany = (params) => {
  return http.fetchGet('/api/company/query.htm', params)
}
//查询所有地址
export const queryAllAddress = (params) => {
  return http.fetchPost('/api/user-address/query-all.do', params)
}
//新增地址
export const addAddress = (params) => {
  return http.fetchPost('/api/user-address/save.do', params)
}
//更新地址
export const updateAddress = (params) => {
  return http.fetchPost('/api/user-address/update.do', params)
}
//更新地址
export const deleteAddress = (params) => {
  return http.fetchPost('/api/user-address/delete.do', params)
}
//
export const queryDistributeList = (params) => {
  return http.fetchGet('/api/task/query-distribute-task-list.htm', params)
}
//
export const queryRegisterList = (params) => {
  return http.fetchGet('/api/task/query-register-task-list.htm', params)
}


// 查询购物车
export const queryCart = (params) => {
  return http.fetchGet('/api/cart/show.do', params)
}
// 加购
export const addCart = (params) => {
  return http.fetchPost('/api/cart/add-cart.do', params)
}
// 删除
export const deleteCart = (params) => {
  return http.fetchPost('/api/cart/delete-item.do', params)
}
// 提交购物车
export const submitCart = (params) => {
  return http.fetchPost('/api/cart/submit-order.do', params)
}
// 修改购买状态
export const changeCart = (params) => {
  return http.fetchPost('/api/cart/update-item-status.do', params)
}

// 商品列表
export const queryModuleContent = (params) => {
  return http.fetchGet('/api/content-module/query-module-content.htm', params)
}

// 提交细胞服务申请信息
export const submitCellService = (params) => {
  return http.fetchPost('/api/cell-service/submit-cell-service.do', params)
}

// 查询细胞分发订单任务信息
export const getCellOrder = (params) => {
  return http.fetchGet('/api/task/get-cell-order-task-detail.htm', params)
}
// 查询细胞服务任务信息
export const getCellService = (params) => {
  return http.fetchGet('/api/task/get-cell-service-task-detail.htm', params)
}



